import React from "react";
import "./styles.css";
import productImg from "./assets/product-img.webp";

const ProductCard = () => {
  return (
    <div className="card">
      <img src={productImg} className="card-img-top" />
      <div className="card-body">
        <p className="title-product">
          NIKE AJ1 Retro High White University Blue Jeans Sepatu Olahraga
        </p>
        <div className="prices">
          <span className="price">Rp. 3.550.000</span>
          <span className="price-discount">Rp. 1000.000</span>
        </div>
      </div>
    </div>
  );
};

export default ProductCard;
